import React from 'react'
import './index.css'
import { getHotSongMenu } from '../../../../../api/song_menu'
import { List } from 'antd-mobile'
import { withRouter } from 'react-router-dom'

class songList extends React.Component {
  state = {
    songList: []
  }
  componentDidMount() {
    this.getSongList()
  }
  songMenu(item) {
    this.props.history.push('/songMenu/' + item.id)
  }
  async getSongList() {
    try {
      let res = await getHotSongMenu()
      this.setState(
        {
          songList: res.result
        },
        () => this.props.isLoad()
      )
    } catch (err) {
      console.log(err)
    }
  }
  render() {
    return (
      <div className='songList' ref={val => (this.recommend = val)}>
        <div className='title'>推荐歌单</div>
        <List>
          <ul>
            {this.state.songList.map((item, index) => {
              return (
                <li key={index} onClick={() => this.songMenu(item)}>
                  <div className='imgBox'>
                    <img
                      src=''
                      alt=''
                      data-url={item.picUrl}
                    />
                  </div>
                  <div>{item.name}</div>
                  <i className='iconfont icon-kefu'> {Math.ceil(item.playCount / 10000)}万</i>
                </li>
              )
            })}
          </ul>
        </List>
      </div>
    )
  }
}

export default withRouter(songList)
